tr:focus {
	outline: none;
	background-color: #eeff7b !important;
}

td:focus {
	outline: none;
	background-color: #daff06 !important;
}

.input-qty-2{
	text-align: right !important;
	width: 100%;
	height: 100%;
	font-weight: 700;
}

.input-qty{
	text-align: center !important;
	font-weight: 600;

	@apply w-full h-16 bg-gray-200 rounded-md placeholder-gray-400 text-3xl font-semibold border-none outline-none mt-2 text-center flex items-center justify-center
}

.input-qty:disabled{
	background: white;
	border: none;
}

.ant-input-number-input{
	font-weight: 700 !important;
	width: 100%;
	height: 100%;
}

.ant-input-group input{
	@apply w-full h-16;
}

.table-container{
	@apply w-full h-full flex flex-col bg-red-500;
}

.table-container .ant-table-wrapper {
	flex: 1;
	overflow: auto;
  }

  .custom-input:focus {
	border-color: #4A5568 !important; /* Warna border saat fokus */
	border-width: 2px !important;
	box-shadow: 0 0 0 2px rgba(66, 153, 225, 0.6); /* Efek shadow saat fokus */
  }